<template>
  <form class="card" @submit.prevent="submit">
    <div class="card-header">
      <h5 class="card-title">Informa√ß√µes da empresa</h5>
    </div>
    <div class="card-body">
      <!-- Raz√£o social -->
      <div class="form-group">
        <label for="ipt-razao">Raz√£o social</label>
        <input type="text" id="ipt-razao" class="form-control" v-model="inputNome" required :disabled="!!empresa.nome">
      </div>
      <!-- Nome fantasia -->
      <div class="form-group">
        <label for="ipt-fantasia">Nome fantasia</label>
        <input type="text" id="ipt-fantasia" class="form-control" v-model="inputFantasia" :disabled="!!empresa.nome_fantasia">
      </div>
      <!-- Tipo de empresa -->
      <div class="form-group">
        <label for="ipt-tipo">Tipo</label>
        <select id="ipt-tipo" class="form-control" v-model="inputTipo" required :disabled="inputTipo !== null">
          <option :value="null" disabled>Selecione</option>
          <option :value="16">EIRELI</option>
          <option :value="6">Firma Individual</option>
          <option :value="8">LTDA - Sociedade Limitada</option>
          <option :value="7">SA - Sociedade An√¥nima</option>
        </select>
      </div>
      <!-- Matriz/Filial -->
      <div class="form-group">
        <label for="ipt-filial">Tipo de sede</label>
        <select id="ipt-filial" class="form-control" v-model="inputMatriz" required :disabled="inputMatriz !== null">
          <option :value="null" disabled>Selecione</option>
          <option :value="1">Matriz</option>
          <option :value="2">Filial</option>
        </select>
      </div>
      <!-- Inscri√ß√£o na junta comercial -->
      <div class="form-group">
        <label for="ipt-junta">Inscri√ß√£o na Junta Comercial</label>
        <input type="text" id="ipt-junta" class="form-control" v-model="inputJunta" required :disabled="!!empresa.inscricao_junta_comercial">
      </div>
      <!-- Data de inscri√ß√£o na junta comercial -->
      <div class="form-group">
        <label for="ipt-juntadata">Data de inscri√ß√£o na Junta Comercial</label>
        <the-mask mask="##/##/####" masked type="tel" id="ipt-juntadata" class="form-control" placeholder="##/##/####" v-model="inputJuntaData" required :class="{'is-invalid': tentativas && !dataValidator(inputJuntaData)}" :disabled="!!empresa.inscricao_junta_comercial_data && empresa.inscricao_junta_comercial_data > '1901-01-01'"></the-mask>
      </div>
      <!-- N√∫mero da altera√ß√£o contratual-->
      <div class="form-group">
        <label for="ipt-alteracao">N√∫mero da altera√ß√£o contratual</label>
        <the-mask mask="####################" type="tel" id="ipt-alteracao" class="form-control" v-model="inputAltContrato" :disabled="!!empresa.numero_alteracao_contratual" required></the-mask>
      </div>
      <!-- Data da altera√ß√£o contratual -->
      <div class="form-group">
        <label for="ipt-datacontratual">Data da altera√ß√£o contratual</label>
        <the-mask mask="##/##/####" masked type="tel" id="ipt-datacontratual" class="form-control" placeholder="##/##/####" v-model="inputAltContratoData" required :class="{'is-invalid': tentativas && !dataValidator(inputAltContratoData)}" :disabled="!!empresa.data_alteracao_contratual && empresa.data_alteracao_contratual > '1901-01-01'"></the-mask>
      </div>
      <!-- Capital social -->
      <div class="form-group">
        <label for="ipt-capital">Valor do capital social</label>
        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <label for="ipt-capital" class="input-group-text">R$</label>
          </div>
          <money id="ipt-capital" class="form-control" v-model="inputCapital" :class="{'is-invalid': tentativas && (!inputCapital || inputCapital === 0)}"></money>
        </div>
      </div>
      <!-- Contrato social -->
      <div class="form-group">
        <label for="ipt-contrato">Contrato social</label>
        <div class="custom-file">
          <input
            type="file"
            class="custom-file-input"
            id="ipt-contrato"
            :accept="limiteContratoFormatos.join(', ')"
            lang="pt-BR"
            :required="!inputContrato"
            @change="onFileChange"
            :class="{'is-invalid': inputContrato && !inputContratoValidSize}"
          >
          <label class="custom-file-label overflow-hidden" for="ipt-contrato" data-browse="Procurar">{{inputContratoName}}</label>
          <small id="emailHelp" class="form-text text-muted" v-if="!inputContrato">Arquivo em formato PDF de at√© {{limiteContratoMegabytes}}MB.</small>
          <div class="invalid-feedback" v-if="!inputContratoValidSize">O arquivo n√£o pode superar {{limiteContratoMegabytes}}MB</div>
        </div>
      </div>
    </div>
    <div class="card-footer text-center">
      <button type="button" class="btn btn-primary" @click="$router.push({name: 'recadpj-menu'})">Cancelar</button>
      <button type="submit" class="btn btn-success">Avan√ßar</button>
    </div>
  </form>
</template>

<script>
  import DateHelper from "../../helpers/DateHelper";
  export default {
    name: "PjDados",
    props: {
      empresa: { required: true, type: Object },
      value: { default: () => ({}), type: Object }
    },
    data: () => ({
      inputNome: '',
      inputFantasia: '',
      inputTipo: null,
      inputMatriz: null,
      inputJunta: '',
      inputJuntaData: '',
      inputAltContrato: '',
      inputAltContratoData: '',
      inputCapital: 0,
      inputContrato: null, //type: File
      //Configs:
      limiteContratoMegabytes: 10,
      limiteContratoFormatos: ["application/pdf"],
      //Cache:
      tentativas: 0 //Tentativas de submit
    }),
    computed: {
      inputContratoName() {
        if (this.inputContrato) return this.inputContrato.name;
        else return 'Insira o arquivo';
      },
      inputContratoValidSize() {
        if (this.inputContrato) return (this.inputContrato.size / 1024) < (this.limiteContratoMegabytes * 1024);
        else return null;
      },
      inputContratoType() {
        return this.inputContrato ? this.inputContrato.type : ''
      }
    },
    methods: {
      // clearForms() {
      //   this.inputNome = '';
      //   this.inputFantasia = '';
      //   this.inputTipo = null;
      //   this.inputMatriz = null;
      //   this.inputJunta = '';
      //   this.inputJuntaData = '';
      //   this.inputAltContrato = '';
      //   this.inputAltContratoData = '';
      //   this.inputCapital = 0;
      //   this.inputContrato = null;
      // },
      submit() {
        this.tentativas++;

        //Confere as valida√ß√µes que precisam ser manuais
        let reprovacoes = 0;
        if (!this.dataValidator(this.inputJuntaData)) reprovacoes++;
        if (!this.dataValidator(this.inputAltContratoData)) reprovacoes++;
        if (!this.inputCapital || this.inputCapital === 0) reprovacoes++;
        if (!this.inputContratoValidSize) reprovacoes++;
        if (reprovacoes > 0) {
          this.$toastr.error('Corrija ' + (reprovacoes > 1 ? 'os campos vermelhos' : 'o campo vermelho'));
          return;
        }

        const dados     = this.value;
        dados.nome      = this.inputNome.trim().toUpperCase();
        dados.fantasia  = this.inputFantasia.trim().toUpperCase();
        dados.tipo      = this.inputTipo;   //6 = FI, 7 = SA, 8 = LTDA, 16 = EIRELI
        dados.matriz    = this.inputMatriz; //1 = Matriz, 2 = Filial
        dados.junta     = this.inputJunta;  //Inscri√ß√£o na Junta Comercial
        dados.juntaData = DateHelper.date_BRparaSQL(this.inputJuntaData);
        dados.altContrato     = this.inputAltContrato; //N√∫mero da altera√ß√£o contratual
        dados.altContratoData = DateHelper.date_BRparaSQL(this.inputAltContratoData);
        dados.capital   = this.inputCapital;
        dados.contrato  = this.inputContrato; //File
        this.$emit('input', dados);
        this.$router.push({ name: 'recadpj-2', params: {empresa: this.empresa} });
      },
      //Rotina interna
      onFileChange(e) {
        const files = e.target.files || e.dataTransfer.files;
        if (!files.length) this.inputContrato = null;
        else this.inputContrato = files[0];
      },
      popularCampos(e) {
        //Obtem os dados de forma inicial
        this.inputNome = e.nome ? e.nome : '';
        this.inputFantasia = e.nome_fantasia ? e.nome_fantasia : '';
        this.inputTipo = e.categoria && (e.categoria === 6 || e.categoria === 7 || e.categoria === 8 || e.categoria === 16) ? e.categoria : null;
        this.inputMatriz = e.matriz ? e.matriz : null;
        this.inputJunta = e.inscricao_junta_comercial ? e.inscricao_junta_comercial : '';
        this.inputJuntaData = (e.inscricao_junta_comercial_data && e.inscricao_junta_comercial_data > "1901-01-01") ? DateHelper.date_SQLparaBR(e.inscricao_junta_comercial_data) : '';
        this.inputAltContrato = e.numero_alteracao_contratual ? e.numero_alteracao_contratual.toString() : '';
        this.inputAltContratoData = (e.data_alteracao_contratual && e.data_alteracao_contratual > "1901-01-01") ? DateHelper.date_SQLparaBR(e.data_alteracao_contratual) : '';
        this.inputCapital = e.capital_social ? e.capital_social : 0;

        //Obtem os dados de forma cacheada (caso eles ja fossem preenchidos anteriormente)
        const dados = this.value;
        if (dados.nome) this.inputNome = dados.nome;
        if (dados.fantasia) this.inputFantasia = dados.fantasia;
        if (dados.tipo) this.inputTipo = dados.tipo;
        if (dados.matriz) this.inputMatriz = dados.matriz;
        if (dados.junta) this.inputJunta = dados.junta;
        if (dados.juntaData) this.inputJuntaData = DateHelper.date_SQLparaBR(dados.juntaData);
        if (dados.altContrato) this.inputAltContrato = dados.altContrato;
        if (dados.altContratoData) this.inputAltContratoData = DateHelper.date_SQLparaBR(dados.altContratoData);
        if (dados.capital) this.inputCapital = dados.capital;
        if (dados.contrato) this.inputContrato = dados.contrato;
      },
      dataValidator(data) {
        if (!data) return false;
        if (data.length !== 10) return false;
        if (parseInt(data.substr(6,4)) < 1900 || parseInt(data.substr(6,4)) > (new Date()).getFullYear()) return false;
        if (parseInt(data.substr(3,2)) > 12) return false;
        if (parseInt(data.substr(0,2)) > 31) return false;
        return true;
      },
    },
    mounted() {
      this.popularCampos(this.empresa);
    },
    beforeRouteLeave(to, from, next) {
      to.params.empresa = this.empresa;
      next();
    }
  }
</script>

